import { Link } from "gatsby";
import React from "react";
import { Dropdown, Pagination } from "react-bootstrap";
import filterIcon from "../../assets/images/product/filterIcon.svg";
import sortIcon from "../../assets/images/product/sortIcon.svg";
import productCateg1 from "../../assets/images/product/productCateg1.png";
import productCateg2 from "../../assets/images/product/productCateg2.png";
import productCateg3 from "../../assets/images/product/productCateg3.png";
import productCateg4 from "../../assets/images/product/productCateg4.png";
import productCateg5 from "../../assets/images/product/productCateg5.png";

import "./product.css";
import ProductCard from "./productCard";

const ProductList = () => {
  return (
    <div className="product--page-content home-content">
      <div className="header-title">
        <div className="container">
          <div className="header-title-text">
            <h2>
              EXPLORE <span>OUR TILE RANGE</span>
            </h2>
            <ul>
              <li>
                <Link to="">Home</Link>
              </li>
              <li>/ Products</li>
            </ul>
          </div>
        </div>
      </div>

      <div className="product-section">
        <div className="container">
          <div className="product-wrapper">
            <div className="product-left">
              <div className="product-section-head">
                <div className="product-filter-section">
                  <Dropdown className="product-filter">
                    <Dropdown.Toggle variant="success" id="dropdown-basic">
                      <img src={filterIcon} alt="filter" /> Filter
                    </Dropdown.Toggle>

                    <Dropdown.Menu>
                      <Dropdown.Item href="#/action-1">Flooring</Dropdown.Item>
                      <Dropdown.Item href="#/action-2">Wall</Dropdown.Item>
                      <Dropdown.Item href="#/action-3">Bathroom</Dropdown.Item>
                    </Dropdown.Menu>
                  </Dropdown>
                  <Dropdown className="product-filter">
                    <Dropdown.Toggle variant="success" id="dropdown-basic">
                      <img src={sortIcon} alt="filter" /> Sort
                    </Dropdown.Toggle>

                    <Dropdown.Menu>
                      <Dropdown.Item href="#/action-1">
                        Most Viewed
                      </Dropdown.Item>
                      <Dropdown.Item href="#/action-2">
                        Newest first
                      </Dropdown.Item>
                      <Dropdown.Item href="#/action-3">
                        Most Populer
                      </Dropdown.Item>
                    </Dropdown.Menu>
                  </Dropdown>
                </div>
                <div className="product-count-section">
                  <span>Items: 276</span>
                  <div className="product-item-count">
                    <span>Items per page:</span>
                    <Dropdown className="product-count product-filter">
                      <Dropdown.Toggle variant="success" id="dropdown-basic">
                        9
                      </Dropdown.Toggle>

                      <Dropdown.Menu>
                        <Dropdown.Item href="#/action-1">20</Dropdown.Item>
                        <Dropdown.Item href="#/action-2">25</Dropdown.Item>
                        <Dropdown.Item href="#/action-3">30</Dropdown.Item>
                      </Dropdown.Menu>
                    </Dropdown>
                  </div>
                </div>
              </div>
              <div className="product-card-section">
                <ProductCard />
                <ProductCard />
                <ProductCard />
                <ProductCard />
                <ProductCard />
                <ProductCard />
                <ProductCard />
                <ProductCard />
                <ProductCard />
              </div>
              <div className="product-pagination blogs-pagination">
                <Pagination>
                  <Pagination.Prev />
                  <Pagination.Item disabled>{1}</Pagination.Item>
                  <Pagination.Item active>{2}</Pagination.Item>
                  <Pagination.Item disabled>{3}</Pagination.Item>
                  <Pagination.Next />
                </Pagination>
              </div>
            </div>
            <div className="product-right">
              <div className="product-category-wrapper">
                <h3>Categories</h3>
                <div className="category-listing">
                  <div className="product-catag-items">
                    <img src={productCateg1} alt="" />
                    <span>Item Name</span>
                  </div>
                  <div className="product-catag-items">
                    <img src={productCateg2} alt="" />
                    <span>Item Name</span>
                  </div>
                  <div className="product-catag-items">
                    <img src={productCateg3} alt="" />
                    <span>Item Name</span>
                  </div>
                  <div className="product-catag-items">
                    <img src={productCateg4} alt="" />
                    <span>Item Name</span>
                  </div>
                  <div className="product-catag-items">
                    <img src={productCateg5} alt="" />
                    <span>Item Name</span>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

      <div className="product-look-section home-contact">
        <div className="home-contact-grid">
          <h2>
            Have a <span> Look</span>
          </h2>
        </div>
      </div>
    </div>
  );
};

export default ProductList;
